.header {
	display: flex;
	max-width: 1200px;
	width: 90%;
	margin-inline: auto;
	justify-content: space-between;
	align-items: center;
	.logo {
		img {
			width: 60px;
		}
	}
	.links {
		display: flex;
		width: 200px;
		justify-content: space-between;
		a {
			text-decoration: none;
			color: #010101;
		}
		.underline {
			border-bottom: 1px solid black;
		}
	}
}